// InfoBar
.InfoBar {
  display: flex;
  justify-content: center;
  align-items: center;
  background: $blue;
  color: #fff;
  border-radius: 10px 10px 0 0;
  height: 50px;
  z-index: 10;
  position: relative;

  .close-chat-btn {
    position: absolute;
    right: 10px;
    width: 20px;
    height: 20px;
    cursor: pointer;
  }
}